.main {
   width:100%;
   height:100vh;
}

.App {
  text-align: center;
}

.leftPanel{
  height: calc(100vh);
  width: 50px;
}


.iconButtons{
  padding-top:5px;
}

.appHeader{
  width:calc(100% - 50px);
  top:0px;
  left:50px;
  padding:8px 8px;
  box-sizing: border-box;
  margin-left:50px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.treeHeading{
  font-size: 20px;
  font-weight: 400;  
}

.logo {
  background: url(data:image/png;base64,) no-repeat left center;
  width: 100px;
  height: 17px;
  margin-top: 10px;
  background-size: cover;
}

.dashboardPanel{
  border-radius: 10px 0px 0px 0px;
  width: calc(100% - 60px);
  min-height: 100vh;
  background: #fff url(images/foliage.png) no-repeat right bottom;
}

.flexRow{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.searchBox {
  width: 450px;
  text-align: left;
}

.searchBox input{
  border: 1px solid #6a6a6a;
  border-radius: 30px;
  background-color: #1d1d1d;
  color: #fff;
  padding:16px;
  font-size: 15px;
  outline:none;
  height: 30px;
}

.searchBox input:active{
  border: 1px solid #6a6a6a;
  outline:none;
}

body {
  margin:0px;
  background: #1d1d1d url(images/colorBand.png) no-repeat 0px top;
  background-size: calc(100%) 4px;
}

.navMenu {
  list-style-type: none;
  padding:0px;
  margin:50px 5px;
}

ul.navMenu li {
  color: #d1d1d1;
  margin-left: 13px;
  height: 60px;
}

ul.navMenu li:hover {
  color: #fff;
}

ul.navMenu li:first-child{
  color: #fff;
  font-size: 10px;
  padding:0px;
  margin-left: 10px;
}

ul.navMenu li .iconItem{
  background:url(images/folder.png) no-repeat right top;
  width: 25px;
  height: 25px;
  opacity: 0.8;
  background-size: cover;  
  margin-left:5px;
}

ul.navMenu li:hover .iconItem{
  opacity: 1;
}

.contentBar{
  width: 80%;
}

.addAction{
  background-color: #0b6ec5;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  text-align: center;
  font-size: 22px;
  font-weight: bold;
}

.searchBtn{
  background-color: #1d1d1d;
  color:#fff;
  font-size: 16px;
  border:none!important;
  margin-top:5px;
}

.btn{
  background-color: #1d1d1d;
  color:#fff;
  font-size: 18px;
  border:none!important;
}

.btn2{
  background-color: #1d1d1d;
  color:#fff;
  border:none!important;
}

#search_query{
  width:400px;
}

#search_query::after {
  content: '\f52b\f52b';
  position: absolute;
  left:0px;
  top:0px;
}

.sideBar {
  width:20%;
  background-color: #fafafa;
  border-right:1px solid #f7f7f7;
  border-radius: 20px 0 0 0;
  height: 100vh;
  color:#626262;
}

.topNavigation ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  padding: 0px;
  margin: 0px;
}

.topNavigation ul li{
  float:left;
  margin-right:50px;
  margin-top:12px;
  padding-left:10px;
  font-size: 12px;
  color:#494949;
}

.topNavigation{
  width: 100%;
  height: 40px;
  background-color: #ffffff;
  border-bottom: 1px solid #e5e5e595;  
  border-radius: 20px 0 0 0;
}

.iconMenu{
  font-size: 18px;
}

.css-1mbxq6w-MuiTreeItem-label{
  font-size: 12px!important;
  padding: 5px;
}

.workspaceGraphic{
  background: url(images/tree.png) no-repeat center top;
  width:100%;
  height: 160px;
  background-size: contain;
}

.flexStart{
  justify-content: flex-start!important;
}

.css-jq3mgl-MuiTreeItem-root{
  width: 100%;
}

.codeBlock{
  padding:10px;
}

.textInfo{
  font-size:12px;
  border-radius:10px;
  margin-right:10px;
}

.codeEditor{
  background-color:#f5f5f5;
  padding:10px;
  box-sizing:border-box;
  height:100px; 
  color:#000000;
  border-radius:10px;
  border: 1px solid #f6f5f5;
  width: 100%;
  font-size:12px;
}

.borderBottom{
  padding-bottom:10px;
  border-bottom: 1px solid rgb(243, 238, 238);
}

.css-zvr402{
  font-size: 12px!important;
}